*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  padding: 0;
  overflow-x: hidden;
}

h1,
p {
  font-family: Lato,sans-serif;
}

.crosshairs {
  position: absolute;
  width: 100%;
  z-index: 2147483645;
}

.crosshairs.hidden {
  display: none;
}

.crosshairs::before,
.crosshairs::after {
  content: "";
  position: absolute;
}

.crosshairs::before {
  height: 24px;
  width: 2px;
  background: #000;
  top: -11px;
}

.crosshairs::after {
  width: 24px;
  height: 2px;
  background: #000;
  left: -11px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.overlay.highlighting {
  background: none;
  border-color: rgba(0, 0, 0, 0.5);
  border-style: solid;
}
.crosshairsTooltiptext {
  color: #fff;
  text-align: left;
  padding: 10px;
  position: absolute;
  z-index: 1;
  margin: 5px;
  font-size: 12px;
  font-family:GE Inspira Sans,sans-serif;
  background: #15252E;
  opacity: 0.97;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.16);
  border-radius: 5px; 
  font-style: regular;
  line-height: 13px;
}

.SpecificSnapshotSelectLi.MuiMenuItem-root.MuiMenuItem-root:hover {
  background-color: var(--color-fill-menu-highlighted) !important;
  color: var(--color-text-common-primary) !important;
}
.SpecificSnapshotSelectLi {
  color: var(--color-text-common-secondary) !important;
  background-color: var(--color-fill-common-secondary) !important; 
  font-family: var(--font-family-body-medium);
  font-size: var(--font-size-body-medium);
  font-weight: var(--font-weight-body-medium);
  line-height: var(--font-line-height-body-medium);
  letter-spacing: var(--font-letter-spacing-body-medium);
}
.SpecificSnapshotSelect{
  color: var(--color-text-common-secondary) !important;
  font-family: var(--font-family-body-medium);
  font-size: var(--font-size-body-medium);
  font-weight: var(--font-weight-body-medium);
  line-height: var(--font-line-height-body-medium);
  letter-spacing: var(--font-letter-spacing-body-medium);
}
.SpecificSnapshotSelectLi.Mui-selected.Mui-selected {
  color: var(--color-text-common-primary) !important;
  background-color: var(--color-fill-menu-selected) !important;
}
.SpecificSnapshotSelectLi.Mui-selected.Mui-selected:hover{
  box-shadow: inset var(--effect-drop-shadow-focus-primary);
  background-color: var(--color-fill-menu-selected) !important;
  color: var(--color-text-common-primary);
}
.SpecificSnapshotSelectLi .MuiInputBase-input{
  background-color: var(--color-fill-common-secondary) !important;
  color: var(--color-text-common-primary) !important;
}
#menu-getSpCategory .MuiPaper-root.MuiMenu-paper.MuiPopover-paper.MuiPaper-rounded {
  background-color: var(--color-fill-common-secondary);
  color: var(--color-text-common-primary) !important;
}










